* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
  background: #181820;
  display: flex;
  justify-content: center;
}
.todo-app {
  /* display: flex;
  flex-direction: column;
  justify-content: start;
  width: 520px;
  min-height: 600px;
  background: #161a2b;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px; */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 80vw;
  min-height: 600px;
  padding:  0 0 20px 0;
  background-color: #21212B;
  text-align: center;
  border-radius: 15px;
  margin-top: 30px;
}

h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 8px;
  border: 2px solid #30303D;
  outline: none;
  width: 60%;
  background: transparent;
  color: #fff;

}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  cursor: pointer;
  font-size: 35px;
  outline: none;
  color: pink; 
}

.submitButton{
  background: none;
  border: none;
  padding: 10px 5px; 
}
.todo-input.edit {
  border: 2px solid #149fff;
}

.todo-button.edit {
  background: linear-gradient(
    90deg,
    rgba(20, 159, 255, 1) 0%,
    rgba(17, 122, 255, 1) 100%
  );
  padding: 16px 22px;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: linear-gradient(
    90deg,
    rgba(45,45, 55) 10%,
    rgba(48, 48, 61, 0.77) 100%
  );

  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}

